"use client";
import React from "react";
import CADModal from "./CADModal";

const ProjectCard = ({ title, description, image, tags, previewUrl, modelUrl }) => {
  return (
    <div className="bg-gray-800 p-4 rounded shadow relative">
      <img src={image} alt={title} className="w-full h-48 object-cover rounded mb-4" />
      <h3 className="text-xl font-semibold mb-2">{title}</h3>
      <p className="text-sm mb-4">{description}</p>
      
      {/* Existing link for preview if available */}
      {previewUrl && (
        <a
          href={previewUrl}
          target="_blank"
          rel="noopener noreferrer"
          className="text-blue-400 hover:underline block mb-2"
        >
          Preview
        </a>
      )}

      {/* Conditionally show CAD modal button for design projects with a modelUrl */}
      {tags.includes("Design") && modelUrl && (
        <div className="mt-2">
          <CADModal modelUrl={modelUrl} />
        </div>
      )}
    </div>
  );
};

export default ProjectCard;
