"use client";
import React, { useState, useEffect } from "react";
import ReactModal from "react-modal";
import CADViewer from "./CADViewer";

export default function CADModal({ modelUrl }) {
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [mounted, setMounted] = useState(false);

  // Use effect to mark the component as mounted and set the app element for ReactModal
  useEffect(() => {
    setMounted(true);
    const appElement = document.getElementById("__next") || document.body;
    ReactModal.setAppElement(appElement);
  }, []);

  // Do not render anything until mounting is complete.
  if (!mounted) {
    return null;
  }

  const openModal = () => setModalIsOpen(true);
  const closeModal = () => setModalIsOpen(false);

  return (
    <>
      <button
        onClick={openModal}
        className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition duration-200"
      >
        View CAD Project
      </button>
      <ReactModal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        contentLabel="CAD Model Viewer"
        style={{
          overlay: {
            backgroundColor: "rgba(0, 0, 0, 0.8)",
            zIndex: 9999,
          },
          content: {
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            width: "90vw",
            height: "90vh",
            padding: 0,
            border: "none",
            backgroundColor: "#222",
            overflow: "hidden",
          },
        }}
      >
        <button
          onClick={closeModal}
          style={{
            position: "absolute",
            top: 10,
            right: 10,
            zIndex: 10,
            padding: "8px 12px",
            backgroundColor: "#fff",
            border: "1px solid #ccc",
            cursor: "pointer",
            fontWeight: "bold",
          }}
        >
          X
        </button>
        <div style={{ width: "100%", height: "100%" }}>
          <CADViewer modelUrl={modelUrl} />
        </div>
      </ReactModal>
    </>
  );
}
