"use client";
import React, { Suspense } from "react";
import { Canvas, useLoader } from "@react-three/fiber";
import { STLLoader } from "three/examples/jsm/loaders/STLLoader";
import { OrbitControls, Bounds, Environment } from "@react-three/drei";

/**
 * Model component: Loads and displays an STL file.
 */
function Model({ url }) {
  const geometry = useLoader(STLLoader, url);
  return (
    <mesh geometry={geometry} castShadow receiveShadow>
      <meshStandardMaterial color="#64b5f6" />
    </mesh>
  );
}

/**
 * CADViewer: Sets up the 3D canvas with improved lighting and environment.
 */
export default function CADViewer({ modelUrl }) {
  return (
    <div style={{ width: "100%", height: "100%" }}>
      <Canvas
        shadows
        camera={{ position: [0, 0, 5], fov: 45 }}
        style={{ backgroundColor: "#222" }}
      >
        <ambientLight intensity={0.3} />
        <directionalLight
          position={[10, 10, 10]}
          intensity={1.0}
          castShadow
          shadow-mapSize={{ width: 1024, height: 1024 }}
        />

        <Suspense fallback={<LoadingFallback />}>
          <Bounds fit clip margin={1}>
            <Model url={modelUrl} />
          </Bounds>
          <Environment preset="city" />
        </Suspense>

        <OrbitControls makeDefault />
      </Canvas>
    </div>
  );
}

/**
 * LoadingFallback: Displays a simple message while the model loads.
 */
function LoadingFallback() {
  return (
    <div
      style={{
        position: "absolute",
        top: "50%",
        left: "50%",
        transform: "translate(-50%, -50%)",
        color: "#fff",
        fontSize: "1.25rem",
      }}
    >
      Loading 3D Model...
    </div>
  );
}
